.credPopupShadow {
  background: rgba(80, 80, 80, 0.9);
  position: absolute;
  top: 0;
  left: 0;
  height: 100%;
  width: 100%;
  z-index: 9999;
}
.credPopupShadow.loading .credPopupTitle ~ * {
  opacity: 0.5 !important;
}

.credPopupShadow.loading > * {
  pointer-events: none !important;
}

.credPopupShadow.loading .credPopupTitle {
  animation: blinker 1s linear infinite;
}

.credPopupForm {
  position: relative;
  border-radius: 10px;
  color: #999;

  font-family: "Raleway", sans-serif;
}
.credPopupData {
  background: white;
  border-radius: 3px;
  padding: 33px;
  position: relative;
}

.credPopupTable {
  width: 320px;
  font-size: -2px;
  color: #444;
  margin-top: 16px;
  border-top: 1px solid #eee;
  padding-top: 18px;
  margin-bottom: 12px;
}
.credPopupTable td {
  padding: 5px 0;
}

.credPopupTable input {
  font-family: "Raleway", sans-serif;
  border: 1px solid #ddd;
  border-radius: 3px;
  height: 35px;
  padding: 0 7px;
  color: #444;
  width: 100px;
  font-size: 15px;
  font-weight: bold;
}
.credPopupForm input[required] {
  width: 212px;
}

#rememberMe + label,
#registerPopupRecommended + label {
  border: 1px solid #ddd;
  border-radius: 3px;
  height: 20px;
  width: 20px;
  float: right;
  margin-left: 10px;
  box-shadow: 0 0 1px 3px white inset;
}

#rememberMe:checked + label,
#registerPopupRecommended:checked + label {
  background-color: #f1cd63;
}

.credPopupError {
  color: white;
  text-align: center;
  line-height: 45px;
  font-weight: bold;
  font-size: 15px;
  display: block;
  background-color: tomato;
  margin: 5px 0;
  border-radius: 4px;
}
.credPopupMainIcon {
  position: absolute;
  top: 26px;
  left: 27px;
  width: 42px;
}
.credPopupTitle {
  line-height: 31px;
  font-size: 21px;
  margin-left: 49px;
  top: 30px;
  color: #444;
  font-weight: bold;
}
.credPopupButton {
  background: #ddd;
  color: #666;
  border: 0;
  font-family: "Raleway", sans-serif;
  height: 36px;
  padding: 0 20px;
  border-radius: 3px;
  font-size: 15px;
  float: right;
  font-weight: bold;
}
.proceedCredPopupButton {
  background: #f1cd63;
  color: #444;
  padding: 0 30px;
  margin-left: 10px;
}

.credPopupHowLogin {
  clear: both;
  float: right;
  font-size: 16px;
  color: #aaa;
  margin-top: 20px;
  text-align: right;
}

.credPopupHowLogin span {
  font-weight: bold;
  color: #d3644a;
  cursor: pointer;
}
