/* ################################### MY LAB ################################### */

#myAccountDetailsTitle {
  padding: 0;
  margin: 0 0 7%;
  font-size: 25px;
  font-family: "Raleway", sans-serif;
  font-weight: bold;
  color: #c38d36;
  display: block;
  width: 100%;
}

#myAccountDetailsTitle.noLabRequests {
  margin: 0;
  color: #b7b7b7;
  text-align: center;
}

#myAccountDetails {
  margin: 0 auto;
  display: table;
  background: white;
  border-radius: 5px;
  box-shadow: 0px 0px 20px rgba(0, 0, 0, 0.2);
  font-family: "Raleway", sans-serif;
  padding: 50px;
  margin-bottom: 5%;
  width: 96%;
  max-width: 600px;
}
#myAccountDetails span,
#myAccountDetails label,
#myAccountDetails > a {
  float: left;
  line-height: 30px;
}
#myAccountDetails span {
  font-weight: bold;
  color: #444444;
}
#myAccountDetails label {
  clear: left;
  width: 42%;
  color: #4d4d4d;
  /* font-weight: bold; */
}

#myAccountDetails a {
  cursor: pointer;
  color: #c38d36;
  font-weight: bold;
}

#myAccountDetails #myAccountMarketing {
  margin-top: 20px;
}

#myAccountDetails #myAccountMarketing.true {
  color: #08a81f;
}

#myAccountDetails #myAccountMarketing.false {
  color: #ee3a0c;
}

#myLabRequestContainer {
  max-width: 1000px;
  margin: 0 auto;
}

.myLabRequestTitle {
  font-weight: 100;
  text-align: center;
}

#myLabRequestContainer .noLabRequests {
  padding: 1vh 9vw;
}

.myLabRequestGrid {
  font-size: 14px;
  color: #666;
  position: relative;
  box-shadow: 0 0 30px rgba(0, 0, 0, 0.1);
  background: white;
  border-collapse: collapse;
}

.myLabRequestGrid td {
  vertical-align: baseline;
  padding: 1em 0.5em;
  height: 80px;
}

.myLabRequestGrid td:first-child {
  vertical-align: top;
  padding-left: 1.1em;
}

.myLabRequestGrid tr:nth-child(odd) td {
  background: rgb(250, 250, 250);
}

.myLabRequestRow img {
  height: 29px;
}

td.myLabRequestMidCol {
  padding-right: 1.3em;
}

td.myLabRequestEndCol {
  text-align: right;
  padding-right: 1.1em;
}

.myLabRequestRowFlavor,
.myLabRequestRowPrice {
  color: #555;
  font-size: 17px;
  display: block;
}

.myLabRequestRowInfo {
  font-size: 12px;
  color: #5d5d5d;
  padding-top: 0.2em;
  display: inline-block;
  line-height: 1em;
}

.myLabRequestRowPoints {
  font-weight: bold;

  font-size: 13px;

  white-space: nowrap;
}

.myLabRequestRowPoints.positive {
  color: #16d016;
}

.myLabRequestRowPoints.negative {
  color: #e35e30;
}

.myLabRequestRowInfo span {
  white-space: nowrap;
  display: inline-block;
}

.myLabRequestRowInfo span:before {
  content: "\2022";
  font-size: 20px;
  vertical-align: middle;
  margin-left: 8px;
}

.myLabRequestRowPrice.free {
  color: #16d016;
}

.myLabRequestTotalSummary {
  font-size: 21px;
  margin: 0 1em 2em;
  text-align: right;
  padding: 30px 3vw;
  background: white;
  box-shadow: 0 0 15px rgba(0, 0, 0, 0.1);
  line-height: 1.5em;
}

.myLabRequestTotalSummaryPoints {
  font-size: 0.7em;
  line-height: 1.6em;
  display: block;
  margin-top: 0.8em;
}

.myLabRequestTotalSummaryPoints b {
  color: #16d016;

  font-weight: bold;
}

.myLabRequestTotalPrice b {
  color: #009b00;
}

.myLabRequestFlask {
  height: 50px;
}

.myLabRequestGridState {
  margin-top: 1em;
  bottom: 1.4em;
  right: 0.9em;
  font-size: 0.85em;
  white-space: nowrap;
}

.myLabRequestGridState:after {
  content: "";
  height: 1em;
  width: 1em;
  display: inline-block;
  border-radius: 1em;
  vertical-align: -0.2em;
  /* white-space: nowrap; */
  margin-left: 0.3em;
  background-color: #d0b016;
}

.myLabRequestGridState.stateR:after,
.myLabRequestGridState.stateN:after {
  background-color: #16d016;
}

.myLabRequestGridState.stateU:after {
  background-color: #e35e30;
}

.myLabRequestGridState.stateP:after {
  background-color: #16a5d0;
}
