/* ################################### HEADER ################################### */

#header {
  background: #111;
  top: 0;
  width: 100%;
  left: 0;
  z-index: 999;
  max-width: 100%;
  height: 105px;
  position: fixed;
}
#headerContainer {
  width: 100%;
  position: relative;
  margin: 0 auto;
  max-width: 1000px;
  display: table;
  height: 100%;
}
#headerLoginText {
  color: #bca076;
  position: absolute;
  right: 10px;
  top: 4px;
}

#headerMenuOpener,
#headerMenuCloser {
  display: none;
}

#headerMenu {
  position: absolute;
  right: 0;
  bottom: 0;
}
#headerMenu a {
  font-size: 15px;
  float: left;
  cursor: pointer;
  color: #8a8072;
  padding: 0 10px 10px;
}

#headerMenu a:hover,
#headerMenu a.selected {
  opacity: 1;
  color: #ffd074;
}
.menuImg {
  display: block;
  margin: 0 auto 6px;
  opacity: 0.8;
  width: 24px;
}
#menuBurger {
  float: right;
}

/* ################################### MEDIA QUERIES ################################### */

@media only screen and (max-width: 850px) {
  #headerMenu {
    display: none;
    position: fixed;
    top: 0;
    right: 0;
    background: #111;
    padding: 40px 40px;
  }

  #headerMenuCloser {
    position: fixed;
    background: rgba(150, 150, 150, 0.7);
    top: 0;
    width: 100%;
    left: 0;
    height: 100%;
  }

  #headerMenuCloser.opened,
  #headerMenu.opened {
    display: block;
  }

  #headerMenuOpener {
    display: block;
    width: 55px;
    position: absolute;
    right: 10px;
    top: 30%;
    padding: 10px;
  }

  #headerMenu a {
    padding: 10px 0;
    font-size: 16px;
    clear: both;
    line-height: 24px;
  }

  #headerMenu a img {
    float: left;
    margin: 0 10px 0 0;
  }
}
@media only screen and (max-width: 750px) {
  #header {
    height: 90px;
  }

  #mainLogo {
    width: 200px;
  }
}

@media only screen and (max-width: 400px) {
  #mainLogoSmall {
    display: block !important;
    margin-left: 10px;
    cursor: pointer;
  }
  #mainLogo {
    display: none;
  }
}
